import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      header: 0
    }
  },
  {
    path: '/photos',
    name: 'Photos',
    component: () => import('../views/Photos.vue') ,
    meta: {
      header: 1
    }
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
